













































import {
  defineComponent, ref, computed,
} from '@vue/composition-api';
import { ElForm } from 'element-ui/types/form';
import { i18n } from '@/i18n';
import validator from '@/utils/validators/rules-validation';
import AppError from '@/utils/error';
import errHandler from '@/utils/error-handler';
import BaseImage from '@/components/base/Image.vue';
import axios from '@/api/axios';
import isDataPipeUrl from '@/utils/is-data-pipe-url';

interface Feedback {
  feedbackType: 0 | 1 | 2 | 3 // 0鞋款1面料2二进制文件3图册
  imageUrl: string // 图片信息
  questionType: 1 | 2 // 1整体环境效果不好；2部件面料效果不好
  content?: string // 反馈内容
}
/**
 * 新增标签
 * @param {Feedback} data
 * @see http://yapi.4dshoetech.local/project/115/interface/api/6116
 */
const apiSaveFeedback = (data: Feedback) => {
  return axios({
    method: 'post',
    url: '/bff-center/feedback/saveFeedback',
    data,
  });
};

function sliceImgUrl(url: string) {
  const anchor = document.createElement('a');
  anchor.href = url;
  if (isDataPipeUrl(url)) {
    return anchor.pathname.replace('/datapipe/repository/web', '');
  }
  return anchor.pathname;
}

export default defineComponent({
  name: 'RenderFeedback',
  components: {
    BaseImage,
  },
  dialogify: {
    // confirmEvent: 'upload',
    props: {
      width: '440px',
      // height: '456px',
      center: true,
      title: i18n.t('model.offline.render_feedback'),
    },
  },
  props: {
    thumbnail: {
      type: String,
      required: true,
    },
  },
  setup(props, { root, emit }) {
    const form = ref<Feedback>({
      questionType: undefined,
      imageUrl: sliceImgUrl(props.thumbnail),
      content: '',
      feedbackType: 3, // 0鞋款1面料2二进制文件3图册
    });
    const rules = {
      questionType: [
        validator.requirement(),
      ],
      content: [
        validator.spaceTrim(),
        validator.charLengthMax(300),
      ],
    };
    const formRef = ref<ElForm>(null);
    const canSubmit = computed(() => {
      return !!form.value.questionType;
    });

    async function submit() {
      try {
        if (!canSubmit.value) {
          throw new AppError(i18n.t('public.error.invalid_param'));
        }
        await apiSaveFeedback(form.value);
        root.$message.success(i18n.t('model.offline.submit_success'));
        emit('confirm');
      } catch (err) {
        root.$message.error({
          message: i18n.t(`public.code_msg._${err.code}`, '') || err.message || i18n.t('model.offline.submit_fail'),
          showClose: true,
          duration: 0,
        });
      }
    }
    return {
      form,
      rules,
      formRef,
      submit,
      canSubmit,
    };
  },
});
